<template>
   <div id="mskContent">
       <p class="title com-title"></p>
       <part-detial>
           <template slot="part1" scope="props">
               <div class="example big">
                   <div class="content" v-for="item in litigation">
                       <p class="typ" v-text="item.typ"></p>
                       <ul class="theme">
                           <li v-text="item.enName"></li>
                           <li><span v-text="item.court"> </span><i v-text="item.date"></i></li>
                       </ul>
                       <ol class="theme-content">
                           <li></li>
                           <li class="sys" v-html="item.reason"> </li>
                       </ol>
                       <ol class="theme-content">
                           <li></li>
                           <li v-text="item.plaintiff"></li>
                       </ol>
                       <ol class="theme-content">
                           <li></li>
                           <li v-text="item.defendant"></li>
                       </ol>
                   </div>
               </div>
           </template>
           <template slot="part2" scope="props">
               <div class="example big">
                   <div class="content" v-for="item in dishonesty">
                       <p class="special-name" v-html="item.enName"></p>
                       <ul class="dishonesty-content">
                           <li> <span></span><em v-text="item.state"></em></li>
                           <li><span></span><em v-text="item.start"></em></li>
                           <li> <span></span><em v-text="item.court"></em></li>
                           <li><span></span><em v-text="item.release"></em></li>
                           <li style="padding:0px"><span></span><em v-text="item.content"></em></li>
                           <li><span></span><em v-text="item.case"></em></li>
                       </ul>
                   </div>
               </div>
           </template>
           <template slot="part4" scope="props">
               <div class="example big">
                   <div class="content" v-for="item in change">
                       <p :class="['typ_base',item.typ1?'typ1':'typ2']" v-text="item.tpy"></p>
                       <p class="company-name" v-text="item.title">
                       </p>
                       <ul class="corporate-ul">
                           <li> <span></span><em v-text="item.before"></em></li>
                           <li><span></span><em v-text="item.date"></em></li>
                           <li> <span></span><em v-text="item.after"></em></li>
                       </ul>
                   </div>
               </div>
           </template>
           <template slot="part3" scope="props">
               <div class="example big ">
                   <div class="content" v-for="item in punish">
                       <p class="typ" v-text="item.typ"></p>
                       <p class="company-name" v-text="item.enName"> </p>
                       <ul class="Indictment-content">
                           <li> <span></span><em v-text="item.case"></em></li>
                           <li><span></span><em v-text="item.court"></em></li>
                           <li> <span></span><em v-text="item.punishType"></em></li>
                           <li><span></span><em v-text="item.date"></em></li>
                           <li><span></span><em v-text="item.content"></em></li>
                       </ul>
                   </div>
               </div>
           </template>
           <template slot="part5" scope="props">
               <div class="example  big">
                   <div class="content" v-for="item in negative">
                   <p class="change" v-text="item.title"> </p>
                       <ul class="dishonesty-content">
                           <li> <span></span><em v-text="item.source"></em></li>
                           <li><span></span><em v-text="item.date"></em></li>
                           <li><span></span><em v-text="item.abstract"></em></li>
                       </ul>
                   </div>
               </div>
           </template>
       </part-detial>
   </div>
</template>
<script>
    import partDetial from "./Company-details/part-detial.vue"
    import axios from "axios"
    import '../assets/css/partDetial.scss'
    export default {
        data(){
            return {
                typ1:false,
                _index:"",
                "litigation":[
                    {"typ":"",
                        "enName":"2015821",
                        "court":"",
                        "date":"2016-03-08",
                        "reason":"20152<em>XXXXXXX</em> 625313598299",
                        "plaintiff":"  ",
                        "defendant":"XXXXXXX"},
                    {"typ":"",
                        "enName":"2015821",
                        "court":"",
                        "date":"2016-03-08",
                        "reason":"20152<em>XXXXXXX</em> 625313598299",
                        "plaintiff":"  ",
                        "defendant":"XXXXXXX"}
                ],
                "dishonesty":[
                    {"enName":"<i></i>",
                        "state":"",
                        "start":"2016-03-08",
                        "court":"",
                        "release":"2016-03-08",
                        "content":"xxxxxxxx",
                        "case":"201628011069"
                    }
                ],
                "punish":[
                    {
                        "typ":"",
                        "enName":"",
                        "case":" [2016] 31",
                        "court":"",
                        "punishType":"",
                        "date":"2016-03-08",
                        "content":""
                    }
                ],
                "change":[
                    {
                        "tpy":"",
                        "title":"2015821",
                        "before":"",
                        "date":"2016-03-08",
                        "after":"",
                        "typ1":true
                    },
                    {
                        "tpy":"",
                        "title":"2015821",
                        "before":"",
                        "date":"2016-03-08",
                        "after":"",
                        "typ1":false
                    }
                ],
                "negative":[
                    {
                        "title":"",
                        "source":"",
                        "date":"2016-03-08",
                        "abstract":"xxxxxx"
                    }
                ]
            }
        },
        components:{
            partDetial
       },
        methods:{
            getJson(index){
                switch(index)
                {
                    case 0:
                        request("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/cloudMap/enterprise/litigation")
                        break;
                    case 1:
                        request("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/cloudMap/enterprise/dishonesty")
                        break;
                    case 2:
                        request("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/cloud/enterprise/punish")
                        break;
                    case 3:
                        request("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/cloudMap/enterprise/change")
                        break;
                    case 4:
                        request("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/cloudMap/enterprise/negative")
                        break;
                    default:
                        console.log(index)
                }
                let that=this;
                function request(url){
                    axios.get(url)
                        .then(function(res){
                            let data=res.data.data;
                            if(data.litigation){
                                that.litigation=data.litigation;
                            }else if(data.dishonesty){
                                that.dishonesty=data.dishonesty;
                            }else if(data.punish){
                                that.punish=data.punish;
                            }else if(data.change){
                                that.change=data.change;
                            }else if(data.negative){
                                that.negative=data.negative;
                            }
                        }).catch(function(res){
                        console.log("data error")
                    })
                }
            }
        },
        props:["indx"],
        created(){
            this.getJson(0);
        },
        watch:{
            indx:function(newValue,oldValue){
                if(newValue!=oldValue){
                    this._index=newValue;
                    this.getJson(this._index);
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
    .title{
        height: 40px;
        background-color: #012a4d;
        text-indent: 20px;
        line-height: 40px;
    }
#mskContent{
    position: absolute;
    padding: 5px;
    z-index: 2;
    width: 826px;
    height: 444px;
    background-color: rgba(1, 19, 27, 0.7);
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    overflow: hidden;
    box-shadow: 0px 0px 40px #12526e inset;
}
</style>


