<template>
    <div id="companyDetails">
        <p class="title"></p>
        <div class="example">
            <div class="detial-content">
                <ul class="company-content">
                    <li><span></span><i v-text="componentDetial.enName"></i></li>
                    <li><span></span><i v-text="componentDetial.typ"></i></li>
                    <li><span></span><i v-text="componentDetial.adress"></i></li>
                    <li> <span></span><i v-text="componentDetial.credit"></i></li>
                    <li><span></span><i v-text="componentDetial.legalPerson"></i></li>
                    <li> <span></span><i v-text="componentDetial.capital"></i></li>
                    <li> <span></span><i v-text="componentDetial.establishDate"></i></li>
                    <li><span></span><i v-text="componentDetial.term"></i></li>
                    <li><span></span><i v-text="componentDetial.range"></i></li>
                </ul>
                <div class="small-change" >
                    <ul class="routerList">
                        <li v-for="(item ,index) in listRouter"  class="com-li" @click="showDetial(index)">
                            {{item.content}}({{item.count}})
                        </li>
                    </ul>
                </div>
                <div class="showArea">
                    <part-detial class="small">
                        <template slot="part1" scope="props">
                            <div class="example big">
                                <div class="content"  v-for="item in componentDetial.litigation">
                                    <p class="typ" v-text="item.typ"></p>
                                    <ul class="spe-spacing">
                                        <li><span v-text="item.adress"> </span><i v-text="item.date"></i></li>
                                    </ul>
                                    <ol class="theme-content">
                                        <li></li>
                                        <li class="sys" v-html="item.reason"> </li>
                                    </ol>
                                    <ol class="theme-content">
                                        <li></li>
                                        <li v-text="item.plaintiff"></li>
                                    </ol>
                                    <ol class="theme-content">
                                        <li></li>
                                        <li v-text="item.defendant"></li>
                                    </ol>
                                </div>
                            </div>
                        </template>
                        <template slot="part2" scope="props">
                            <div class="example big" >
                                <div class="content" v-for="item in componentDetial.dishonesty">
                                    <ul class="dishonesty-content">
                                        <li> <span></span><em v-text="item.state"></em></li>
                                        <li><span></span><em v-text="item.start"></em></li>
                                        <li style="padding:0px"><span></span><em v-text="item.content"></em></li>
                                        <li> <span></span><em v-text="item.court"></em></li>
                                    </ul>
                                </div>
                            </div>
                        </template>
                        <template slot="part4" scope="props">
                            <div class="example big" >
                                <div class="content" v-for="item in componentDetial.change">
                                    <p :class="['typ_base',item.typ1?'typ1':'typ2']" v-text="item.tpy"></p>
                                    <ul class="corporate-ul spe-pad">
                                        <li> <span></span><em v-text="item.before"></em></li>
                                        <li><span></span><em v-text="item.date"></em></li>
                                        <li> <span></span><em v-text="item.after"></em></li>
                                    </ul>
                                </div>
                            </div>
                        </template>
                        <template slot="part3" scope="props">
                            <div class="example big">
                                <div class="content" v-for="item in componentDetial.punish">
                                    <p class="typ" v-text="item.typ"></p>
                                    <ul class="Indictment-content spe-pad">
                                        <li> <span></span><em v-text="item.case"></em></li>
                                        <li><span></span><em v-text="item.court"></em></li>
                                        <li> <span></span><em v-text="item.punishType"></em></li>
                                        <li><span></span><em v-text="item.date"></em></li>
                                        <li><span></span><em v-text="item.content"></em></li>
                                    </ul>
                                </div>
                            </div>
                        </template>
                        <template slot="part5" scope="props">
                            <div class="example  big">
                                <div class="content" v-for="item in componentDetial.negative">
                                    <p class="change" v-text="item.title"> </p>
                                    <ul class="dishonesty-content">
                                        <li> <span></span><em v-text="item.source"></em></li>
                                        <li><span></span><em v-text="item.date"></em></li>
                                        <li><span></span><em v-text="item.abstract"></em></li>
                                    </ul>
                                </div>
                            </div>
                        </template>
                    </part-detial>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import partDetial from '../Company-details/part-detial.vue'
    import axios from "axios"
export default {
    name:'Indictment',
    data(){
        return {
            listRouter:[
                {path:'/showInfo/litigation',content:'',count:6,total:0,},
                {path:'/showInfo/Dishonesty',content:'',count:5,total:0,},
                {path:'/showInfo/Indictment',content:'',count:4,total:0,},
                {path:'/showInfo/corporateChange',content:'',count:8,total:0,},
                {path:'/showInfo/Opinion',content:'',count:6,total:0,}
            ],
            seletedRouter:1,
            "componentDetial":{
                "enName":"xxxx",
                "typ":"",
                "adress":"BXXXXX",
                "credit":"911101011402002G",
                "legalPerson":"",
                "capital":"1000",
                "establishDate":"20151201",
                "term":"20151201       20351131",
                "range":"XXXXXXXCCCCVVVVGGG",
                "litigation":[
                    {   "typ":"",
                        "adress":"B30",
                        "date":"2016-03-08",
                        "reason":"20152<em>XXXXXXX</em> 625313598299",
                        "plaintiff":"  ",
                        "defendant":"XXXXXXX"
                    }
                ],
                "dishonesty":[
                    {
                        "state":"",
                        "start":"2016-03-08",
                        "content":"xxxxxxxx",
                        "court":""
                    }
                ],
                "punish":[
                    {
                        "typ":"",
                        "case":" [2016] 31",
                        "court":"",
                        "punishType":"",
                        "date":"2016-03-08",
                        "content":""
                    }
                ],
                "change":[
                    {
                        "tpy":"",
                        "before":"",
                        "date":"2016-03-08",
                        "after":"",
                        "typ1":true
                    },
                    {
                        "tpy":"",
                        "before":"",
                        "date":"2016-03-08",
                        "after":"",
                        "typ1":false
                    }
                ],
                "negative":[
                    {
                        "title":"",
                        "source":"",
                        "date":"2016-03-08",
                        "abstract":"xxxxxx"
                    }
                ]
            }
        }
    },
    methods:{
        showDetial(index){
            let examples=document.querySelectorAll(".small .big");
            let li_list=document.querySelectorAll(".com-li");
            var  addStr,_str,removeStr;
            for(var i=0;i<examples.length;i++){
                 _str=" "+li_list[i].getAttribute("class")+" ";
                 removeStr = _str.replace(/hover/g,"");    //   
                examples[i].style.display="none";
                li_list[i].setAttribute("class",removeStr );
            }
            addStr=_str.concat("hover");
            examples[index].style.display="block"
            li_list[index].setAttribute("class",addStr );

        },
        getJson(params){
            let that=this;
            axios.get("http://192.168.1.68:8069/kinship/enterprise/enterpriseDetial?a=3",params)
                .then(function(res){
                    if(res.data.code==501){
                        axios.get("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/kinship/enterprise/enterpriseDetial",params)
                            .then(function(res){
                                let data=res.data.data;
                                that.componentDetial=data.componentDetial;
                                let arr=["litigation","dishonesty","punish","change","negative"]
                                for(var i=0;i<that.listRouter.length;i++){
                                    let index=arr[i];
                                    that.listRouter[i].count=data.componentDetial[index].length;
                                }
                            }).catch(function(res){
                            console.log("data error")
                        })

                    }else{
                        let data=res.data.data;
                        that.componentDetial=data.componentDetial;
                        let arr=["litigation","dishonesty","punish","change","negative"]
                        for(var i=0;i<that.listRouter.length;i++){
                            let index=arr[i];
                            that.listRouter[i].count=data.componentDetial[index].length;
                        }
                    }
                }).catch(function(res){
                console.log("data error")
            })
        }
    },
    props:["detial"],
    watch:{
        detial:function(newValue, oldValue){
            if(newValue==true){
                this.showDetial(0);
            }
        }
    },
    components:{
        partDetial
    },
    created(){
        this.getJson({
            params: {
                enName: "",
            }
        })
    }
}
</script>
<style lang="scss" scoped>
    @import '../../assets/css/public';
    #companyDetails {
        position: fixed;
        box-sizing: border-box;
        padding: 5px;
        z-index: 2;
        width: 840px;
        height: 800px;
        background-color:rgba(0,17,27,0.6);
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        color: #fff;
        box-shadow: 0px 0px 40px #0e4760 inset;
        .spe-pad{
            padding-top:24px;
        }
        .spe-spacing{
            padding-top:15px;
            color:#fff;
            li{
                padding:6px;
            }
            span{
                font-weight: 600;
            }
            i{
                float:right;
                padding-right:6px;
                color:#00a1dd;
            }
        }
        .title{
            height: 40px;
            background-color: #012a4d;
            text-indent: 20px;
            line-height: 40px;
        }
        .detial-content{
            position: relative;
            margin-top: 10px;
            height: 100px;
            text-indent: 15px;
            .company-content{
                overflow: hidden;
                li{
                    padding-bottom: 20px;
                    span{
                        color:#24a7d8;
                        display: block;
                        float:left;
                        width:20%;
                        padding-left:12px;
                    }
                    i{
                        display:inline-block;
                        width:610px;
                        @include whiteSpace;
                    }
                }
            }
        }
        .showArea{
            width:100%;
            max-height:358px;
            position: absolute;
            margin-top:10px;
            left:0px;
            top:376px;
            overflow: hidden;
            box-sizing: border-box;
            background-color:rgba(0,0,0,0.6);
            text-indent: 0px;
            font-family: "";
            .small{
                margin-top:0px;
            }
        }
    }
    .small-change{
        .routerList{
            .router-link-active{
                border-bottom: 3px solid #00ccff;
                color:#00ccff;
            }
            overflow: hidden;
            margin:30px 100px 15px 83px;
            border-bottom: 1px solid #014b69;
            .com-li{
                float:left;
                padding:5px 30px;
                cursor: pointer;
            }
            .com-li:hover{
                color:#03a7bc;
            }
            .hover{
                border-bottom: 3px solid #00ccff;
                color:#00ccff;
            }
        }
    }
    .example{
        margin-top:0!important;
        margin-bottom:10px;
    }
   .content:first-of-type{
        margin-top:0!important;
    }

</style>


