<template>
    <div id="showInfo">
        <div class="big-change">
                <dl @click="bigChange(0)">
                    <dt>
                        <img src="../../assets/img/icon-info.png" alt="">
                    </dt>
                    <dd>
                        <p class="sys"><span class="litigation-info"></span><em class="litigation-count">{{litigationData.litigationTotal}}</em></p>
                        <p class="shy"><span></span><em>{{litigationData.litigationAverage}}</em></p>
                    </dd>
                </dl>
                <dl @click="bigChange(1)">
                <dt>
                    <img src="../../assets/img/icon-info1.png" alt="">
                </dt>
                <dd>
                    <p class="sys"><span class="litigation-info"></span><em class="litigation-count">{{dishonestyData.dishonestyTotal}}</em></p>
                    <p class="shy"><span></span><em>{{dishonestyData.dishonestyAverage}}</em></p>
                </dd>
            </dl>
                <dl @click="bigChange(2)">
                <dt>
                    <img src="../../assets/img/icon-info2.png" alt="">
                </dt>
                <dd>
                    <p class="sys"><span class="litigation-info"></span><em class="litigation-count">{{punishData.punishTotal}}</em></p>
                    <p class="shy"><span></span><em>{{punishData.punishAverage}}</em></p>
                </dd>
            </dl>
                <dl @click="bigChange(3)">
                <dt>
                    <img src="../../assets/img/icon-font3.png" alt="">
                </dt>
                <dd>
                    <p class="sys"><span class="litigation-info"></span><em class="litigation-count">{{changeData.changeTotal}}</em></p>
                    <p class="shy"><span></span><em>{{changeData.changeAverage}}</em></p>
                </dd>
            </dl>
                <dl @click="bigChange(4)">
                <dt>
                    <img src="../../assets/img/icon-info4.png" alt="">
                </dt>
                <dd>
                    <p class="sys"><span class="litigation-info"></span><em class="litigation-count">{{negativeData.negativeTotal}}</em></p>
                    <p class="shy"><span></span><em>{{negativeData.negativeAverage}}</em></p>
                </dd>
            </dl>
        </div>
        <div class="relationGraph">
            <div class="show">
                <button class="only-show"></button>
                <button class="all-show"></button>
            </div>
            <div class="tubiao">
                <div id="tubiao" style="height:700px;width:600px"></div>
            </div>
        </div>
        <div class="info">
            <div class="example" v-for="i in enterpriseList">
                <p v-text="i.enName"></p>
                <ul>
                    <li><span>:</span><i v-text="i.litigation"></i></li>
                    <li><span>:</span><i v-text="i.dishonesty"></i></li>
                    <li><span>:</span><i v-text="i.punish"></i></li>
                    <li><span>:</span><i v-text="i.change"></i></li>
                    <li><span>:</span><i v-text="i.negative"></i></li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
import '../../assets/css/showinfo.scss'
import echarts from "echarts/lib/echarts"
import "echarts/lib/chart/graph"
import "echarts/lib/component/tooltip"
import "echarts/lib/component/legend"
import axios from "axios"
export default {
    name: "showInfo",
    data() {
        return {
            conditionSmall: false,
            conditionBig: false,
            start_ok: false,
             litigationData: {litigationTotal: 100, "litigationAverage": 20},
             "dishonestyData": {"dishonestyTotal": 100, "dishonestyAverage": 20},
             "punishData": {"punishTotal": 100, "punishAverage": 20},
             "changeData": {"changeTotal": 100, "changeAverage": 20},
             "negativeData": {"negativeTotal": 100, "negativeAverage": 20},
            "enterpriseList": [
                {
                    "enName": "xx",
                    "litigation": "",
                    "dishonesty": 2,
                    "punish": 3,
                    "change": 5,
                    "negative": 7
                },
                {
                    "enName": "xx",
                    "litigation": 3,
                    "dishonesty": 2,
                    "punish": "",
                    "change": 5,
                    "negative": 7
                },
                {
                    "enName": "xx",
                    "litigation": "",
                    "dishonesty": 2,
                    "punish": 3,
                    "change": 5,
                    "negative": 7
                }
            ],
            list:[],
            "copyData":[
                {
                    "name":"",
                    "data":[
                        {"enName":"1","outport":1,"position":""},
                        {"enName":"2","outport":1,"position":""},
                        {"enName":"3","outport":0,"position":""}
                    ]
                },
                {
                    "name":"",
                    "data":[
                        {"enName":"6","outport":1,"position":""},
                        {"enName":"7","outport":1,"position":""},
                        {"enName":"4","outport":0,"position":""}
                    ]
                }
            ],
            "node":[
                {
                    "name": "", "x": 200, "y": 60,
                    "symbolSize": 65,"category":2,
                    "color":"#41a0fe"
                },
                {
                    "name": "7", "x": 200, "y": 60,
                    "symbolSize": 72,"category":1,
                    "color":"#f86b4d"
                },
                {
                    "name": "8", "x": 200, "y": 60,
                    "symbolSize": 54,"category":1,
                    "color":"#f86b4d"
                },
                {
                    "name": "9", "x": 200, "y": 60,
                    "symbolSize": 54,"category":1,
                    "color":"#f86b4d"
                },


                {
                    "name": "\n", "x": 200, "y": 60,
                    "symbolSize": 63,"category":2,
                    "color":"#41a0fe"
                },
                {
                    "name": "4", "x": 200, "y": 60,
                    "symbolSize": 72,"category":0,
                    "color":"#06377c"
                },
                {
                    "name": "5", "x": 200, "y": 60,
                    "symbolSize": 54,"category":0,
                    "color":"#06377c"
                },
                {
                    "name": "6", "x": 200, "y": 60,
                    "symbolSize": 54,"category":0,
                    "color":"#06377c"
                },
                {
                    "name":"\n3", "x": 200, "y": 60,
                    "symbolSize": 68,"category":2,
                    "color":"#41a0fe"
                },
                {
                    "name": "1", "x": 200, "y": 60,
                    "symbolSize": 72,"category":1,
                    "color":"#f86b4d"
                },
                {
                    "name": "2", "x": 200, "y": 60,
                    "symbolSize": 54,"category":0,
                    "color":"#06377c"
                },
                {
                    "name": "3", "x": 200, "y": 60,
                    "symbolSize": 54,"category":0,
                    "color":"#06377c"
                },
                {
                    "name": "", "x": 330, "y": 60,
                    "symbolSize":0,
                }
            ],
            "nodeList":[
                {"source": "7", "target": ""},
                {"source": "8", "target": ""},
                {"source": "9", "target":""},
                {"source": "9", "target": "4"},


                {"source": "1", "target": "\n3"},
                {"source": "1", "target": "6"},
                {"source": "2", "target": "\n3"},
                {"source": "2", "target": "8"},
                {"source": "3", "target": "\n3"},

                {"source": "4", "target": "\n"},
                {"source": "4", "target": "6"},
                {"source": "5", "target": "\n"},
                {"source": "6", "target": "\n"}
            ],
            obj1: {
                big: 0,
                index: ""
            }
        }
    },
    props:["administrationName"],
    mounted(){
        this.initPage();
        this.getUrl("http://192.168.1.68:8069/kinship/enterprise/analysisData?a=1","http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/kinship/enterprise/enterpriseList",{
            params:{
                administrationName:this.administrationName,
            }
        })
    },
    methods: {
        bigChange(index) {
            this.obj1.index = index;
            app.whichSlot(this.obj1);
        },
        initPage(){
            let _this = this;
            this.getJson({
                params:{
                    administrationName:this.administrationName,
                }
            })
            this.addData();
        },
        initChart(arr1, arr){
            var myChart = echarts.init(document.getElementById('tubiao'));
            myChart.setOption({
                tooltip: {
                    show:false,
                    trigger: 'item',
                },
                legend: {
                    itemWidth:14,
                    itemHeight:14,
                    borderColor:"#fff",
                    x: 'right',
                    data:  [
                        {
                            name:'',
                            icon: 'circle'
                        },
                        {
                            name:'',
                            icon: 'circle'
                        },
                        {
                            name:'',
                            icon: 'circle'
                        }
                    ],
                    textStyle:{ color:"#fff"},
                    inactiveColor:"#fff"
                },
                series: [{
                        type: 'graph',
                        layout: 'force',
                        edgeSymbol: ['arrow'],
                        categories: [
                            {
                                name: ''
                            },
                            {
                                name: ''
                            },
                            {
                                name: ''
                            }
                        ],
                        itemStyle:{
                            normal:{
                                shadowColor:"rgba(255,255,255,0.4)",
                                shadowBlur:"30"
                            }
                      },
                        force: {
                            gravity: 0.08,
                            edgeLength: 8,
                            repulsion: 50
                        },
                        grid: {
                            top: '15%',
                            left: '1%',
                            right: '1%',
                            bottom: '15%',
                            containLabel: true
                        },

                        symbolSize: 64,
                        label: {
                            normal: {
                                show: true,
                                textStyle: {
                                    fontSize: 16,
                                    color: '#fff',
                                }
                            }
                        },
                        lineStyle: {
                            normal: {
                                color: '#52a2a3',
                                curveness: 0.2,
                                width: 2
                            }
                        },
                    data:arr,
                    links:arr1,
                    }
                ]
            });
            myChart.on('click', function (params) {
                if (params.componentType === 'markPoint') {
                    //  markPoint 
                }
                else if (params.componentType === 'series') {
                    if (params.seriesType === 'graph') {
                        if(params.data.category==0 || params.data.category==1){
                            app.comDetial(true);
                        }
                    }
                }

            });
        },
        getJson(params){
            let that=this;
            axios.get("http://192.168.1.68:8069/kinship/enterprise/analysisData?a=2",params)
                .then(function(res){
                    if(res.data.code==501){
                        axios.get("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/kinship/enterprise/analysisData",params)
                            .then(function(res){
                                let data=res.data.data;
                                that.litigationData=data.litigationData;
                                that.dishonestyData=data.dishonestyData;
                                that.punishData=data.punishData;
                                that.changeData=data.changeData;
                                that.negativeData=data.negativeData;
                            }).catch(function(res){
                            console.log("data error")
                        })

                    }else{
                        let data=res.data.data;
                        that.litigationData=data.litigationData;
                        that.dishonestyData=data.dishonestyData;
                        that.punishData=data.punishData;
                        that.changeData=data.changeData;
                        that.negativeData=data.negativeData;
                    }
                }).catch(function(res){
                console.log("data error")
            })
        },
        getData(params){
            let that=this;
            axios.get("http://192.168.1.68:8069/kinship/enterprise/analysisData?a=2",params)
                .then(function(res){
                    if(res.data.code==501){
                        axios.get("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/kinship/enterprise/analysisData",params)
                            .then(function(res){
                                let data=res.data.data;
                                that.litigationData=data.litigationData;
                                that.dishonestyData=data.dishonestyData;
                                that.punishData=data.punishData;
                                that.changeData=data.changeData;
                                that.negativeData=data.negativeData;
                            }).catch(function(res){
                            console.log("data error")
                        })

                    }else{
                        let data=res.data.data;
                        that.litigationData=data.litigationData;
                        that.dishonestyData=data.dishonestyData;
                        that.punishData=data.punishData;
                        that.changeData=data.changeData;
                        that.negativeData=data.negativeData;
                    }
                }).catch(function(res){
                console.log("data error")
            })
        },
//        getJson(){
//            let that=this;
//            //   mock
//            axios.get("http://mock.eolinker.com/MkWv8GH51af9c8cf43c84fc9e2e61cb2ffe494e74d95d83?uri=/kinship/enterprise/enterpriseList",{
//                params:{
//                    administrationName:this.administrationName,
//                }
//            })
//                .then(function(res){
//                    that.enterpriseList=res.data.data.enterpriseList;
//                }).catch(function(res){
//                console.log("data error")
//            })
//
//            //   ecahrt
//
//        },
        addData(){
            let len=this.copyData.length;
            let arr=[];
            let arr1=[];
            let sonLen;
            for(var i=0;i<len;i++){
                let $t=this.copyData[i];
                let obj= {
                    "name":"aa", "x": 200, "y": 60,
                    "symbolSize": 54,"category":2,
                    "itemStyle": {
                        "normal": {
                            "color": "#41a0fe"
                        }
                    }
                };
                obj.name=$t.name;
                sonLen=$t.data.length;
                for(var m=0;m<sonLen;m++){
                    let obj= {
                        "name":"aa", "x": 200, "y": 60,
                        "symbolSize": 54,"category":1,
                        "itemStyle": {
                            "normal": {
                                "color": "#41a0fe"
                            }
                        }
                    };
                    let obj1={"source": "7", "target": ""};
                    obj1.source=$t.data[m].enName;
                    obj1.target=$t.name;
                    obj.name=$t.data[m].enName;
                    if($t.data[m].outport==1){
                        obj.category=1;      //   
                        obj.itemStyle.normal.color="#f86b4d"
                    }else{
                        obj.category=0;
                        obj.itemStyle.normal.color="#06377c";
                    }
                    arr.push(obj)
                    arr1.push(obj1)
                }
                arr.push(obj)
                arr.push({
                    "name": "", "x": 330, "y": 60,
                    "symbolSize":0,
                })
            }
            this.initChart(arr1, arr);

        }
    }
}
</script>




